@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --color-primary: #808080;
  --color-secondary: #1CA0F6;
  --color-white: #FAFAFA;
  --color-black: #222222;

}

body{
  max-inline-size: 400px;
  margin: 0 auto;
  background: #FAFAFA;
  background: var(--color-white);
  font-family: 'Roboto', sans-serif;
  outline: 1px solid red;
  color: #222222;
  color: var(--color-black);

}
.header{
  text-align: center;
}
.header__title{
  font-weight: 700;
  font-size: 2.5rem;
}
.header__subtitle{
  text-align: center;
  font-weight: 500;
  font-size: 1rem;
  color: #808080;
  color: var(--color-primary);
}
.header__search{
  text-align: center;
  margin-block: 25px;
}
.input__search{
  inline-size: 50%;
  padding: 8px 15px;
  border-radius: 5px;
  outline: none;
  border: 0.5px solid #1CA0F6;
  border: 0.5px solid var(--color-secondary);
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.card__list{
  list-style: none;
  padding: 0;
  margin-inline: 25px;
  -webkit-padding-after: 3rem;
          padding-block-end: 3rem;
}
.card__item{
  position: relative;
  -webkit-margin-before: 25px;
          margin-block-start: 25px;
  border-radius: 5px;
  background: #8080801f;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.btn__checked{
  position: absolute;
  top: 1.2rem;
  left: .5rem;
  font-size: 1rem;
  color: #808080;
  color: var(--color-primary);
}
.btn__checked-active, .btn__checked:hover{
  color: #12bb36;
}
.card__description{
  -webkit-padding-start: 3rem;
          padding-inline-start: 3rem;
  font-size: 1rem;
  padding-block: 1rem;
}
.card__description-active{
  text-decoration: line-through;
  color: #808080;
  color: var(--color-primary);
}
.btn__close{
  position: absolute;
  right: .5rem;
  top: -.6rem;
  font-size: 2rem;
  color: #808080;
  color: var(--color-primary);
}
.btn__close:hover{
  color: #fb6161;
}
.btn{
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: none;
  background: #1CA0F6;
  background: var(--color-secondary);
  font-size: 3rem;
  color: #FAFAFA;
  color: var(--color-white);
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 2;
}

.ModalBackground{
  background: rgba(32,35,41,.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  z-index: 1;
}
form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 40px;
  display: flex;
  align-content: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  color: #1E1E1F;
  margin-bottom: 26px;
}

textarea {
  background-color: #F9FBFC;
  border: 2px, solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea:-ms-input-placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea::placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button-add {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-cancel {
  background: transparent;
}

